/* ----------------------------------------
  ONLY ENTRIES THAT LIVE WITHIN
  THE .entry-content DIV MAY LIVE HERE
---------------------------------------- */

.entry-content {
  position: relative;
  z-index: 2;
  padding: 8rem 0 8rem 0;

  h2 {
    font-family: $body-font;
  }
  @include mq('tablet-wide', 'min') {
    margin: 0 0 6rem 0;
  }

  .wp-block-group {
    margin-bottom: 2rem;

    &.centered {
      .wp-block-group__inner-container {
        max-width: 568px;
        padding: 0 2rem;

        @include mq('tablet-wide', 'min') {
          padding: 0 0;
        }

        h2 {
          font-size: 32px;
          line-height: 158%;
          font-weight: 100;
          margin: 0 auto;
          margin-bottom: 2rem;
        }
      }
    }
  }

  > p {
    max-width: calc(#{$max-narrow-content-width-desktop});
    margin: 0 auto;
    padding: 0 2rem;
  }

  //.wp-block-group {
  p:last-child {
    margin-bottom: 3rem;
  }

  > h2,
  > h3,
  > h4,
  > h5,
  > h6 {
    max-width: calc(#{$max-narrow-content-width-desktop});
    font-size: 24px;
    line-height: 158%;
    font-weight: 600;
    margin: 0 auto;
    margin-bottom: 2rem;
    padding: 0 2rem;
  }

  > h3 {
    font-size: 22px;
  }

  > h4 {
    font-size: 20px;
  }

  > h5 {
    font-size: 18px;
  }

  h3 {
    font-family: $body-sans-font;
    font-size: 22px;
    line-height: 173%;
    margin-bottom: 1.5rem;
    padding-top: 1rem;
  }

  h2:not(:first-child) {
    margin-top: 3rem;
  }
}
